import "core-js/modules/es.array.concat";
import api from './index';
import apiSetting from '@/config';
var version = apiSetting.version; // 企业微信登录

export var login = function login(data) {
  return api.post("/login/", data);
}; // 文件上传

export var upload = function upload(data) {
  return api.post("".concat(version, "/upload/"), data);
}; // 提交项目表单

export var postCreatProject = function postCreatProject(data) {
  return api.post("".concat(version, "/project/create"), data);
}; // 获取需要审批的项目列表

export var getProjects = function getProjects(params) {
  return api.get("".concat(version, "/project/list"), params);
};
export var getProject = function getProject(projectID) {
  return api.get("".concat(version, "/project/").concat(projectID));
};
export var auditProject = function auditProject(projectID, data) {
  return api.patch("".concat(version, "/project/audit/").concat(projectID), data);
};
export var auditRight = function auditRight(projectID) {
  return api.get("".concat(version, "/project/right/").concat(projectID));
};